export default {
	data() {
		return {
			swiperData: []
		};
	},
	created() {
		this.getSwiperImg()
	},
	methods: {
		async getSwiperImg() {
			const res = await this.myRequest({
				url: '/index/swiper'
			});
			this.swiperData=res.data[0].data.swiper
		}
	}
};
